.appsListWrapper {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  text-align: center;
  background-color: #282c34;
  border-radius: 38px;
  color: honeydew;
}

.appsList {
  padding: 0;
  display: flex;
  justify-content: center;
}

.listItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style: none;
  background: linear-gradient(102deg, #534af72a -4.42%, #2290f72a 103.64%);
  border-radius: 24px;
  border: 1px solid rgba(16, 132, 255, 0.11);
  box-shadow: 0px 0px 40px 0px rgba(16, 132, 255, 0.1), 0px 0px 32px 0px rgba(16, 132, 255, 0.2) inset,
    0px 0px 32px 0px rgba(250, 36, 61, 0.05) inset;
  padding: 2rem;
  max-width: 300px;
}

.itemLogo {
  height: 100px;
  pointer-events: none;
}

.itemTitle {
  font-weight: 500;
  font-size: 24px;
  margin-bottom: 8px;
}

.itemCompatibility {
  margin: 0;
  color: rgba(240, 255, 240, 0.7);
}

.itemDescription {
  margin: 2rem 0;
  color: rgba(240, 255, 240, 0.7);
}

.appStoreLink {
  border-radius: 100%;
}
.appStoreLink:hover {
  cursor: pointer;
  opacity: 0.5;
}

.appStoreLogo {
  height: 50px;
  width: 50px;
  filter: invert(100%);
  border-radius: 100%;
}
