.header {
  display: flex;
  justify-content: space-between;
  gap: 8px;
  align-items: center;
  margin-top: 1rem;
}

.companyName {
  text-align: left;
  flex-shrink: 1;
}

.button {
  background-image: linear-gradient(102deg, #534af7 -4.42%, #2290f7 103.64%);
  padding: 20px 40px;
  border-style: none;
  border-radius: 38px;
  color: honeydew;
  font-weight: 700;
  font-size: 1rem;
  flex-shrink: 0;
  text-decoration: none;
}

.button:hover {
  cursor: pointer;
  opacity: 0.9;
}
